.sidebar {
  width: 220px;
  background: #222c3a;
  color: #fff;
  display: flex;
  flex-direction: column;
  border-radius: 0 24px 24px 0;
  box-shadow: 2px 0 16px rgba(34,49,63,0.10);
  padding: 2em 0 2em 0;
}
.sidebar-header {
  font-size: 1.4rem;
  font-weight: 700;
  color: #2e86de;
  text-align: center;
  margin-bottom: 2em;
  letter-spacing: 1px;
}
.sidebar-nav {
  display: flex;
  flex-direction: column;
  gap: 0.5em;
}
.nav-link {
  display: flex;
  align-items: center;
  gap: 1em;
  padding: 0.9em 2em;
  color: #fff;
  font-size: 1.08rem;
  border-radius: 18px 0 0 18px;
  transition: background 0.18s, color 0.18s;
}
.nav-link.active, .nav-link:hover {
  background: #2e86de;
  color: #fff;
}
.nav-icon {
  font-size: 1.3em;
}

.sidebar-footer {
  margin-top: auto;
  padding: 1em 2em;
  display: flex;
  justify-content: center;
}
.sidebar-footer .small-btn {
  background: transparent;
  color: #fff;
  border: 1px solid rgba(255,255,255,0.12);
  padding: 0.4em 0.8em;
  border-radius: 8px;
  cursor: pointer;
}
.sidebar-footer .small-btn:hover {
  background: rgba(255,255,255,0.06);
}
